import React from "react"
import { Header } from "../components/Header"
import { PageHeadBanner } from "../components/PageHeadBanner"
import { DescriptionSection } from "../components/DescriptionSection"
import { MediaSection } from "../components/MediaSection"
import { ContactSection } from "../components/ContactSection"
import SEO from "../components/seo"
import {
  createMuiTheme,
  responsiveFontSizes,
  ThemeProvider,
} from "@material-ui/core/styles"
import "./index.css"
import { AwardsSection } from "../components/AwardsSection"

const theme = responsiveFontSizes(
  createMuiTheme({
    typography: {
      h1: {
        fontFamily: "Barlow, sans-serif",
        fontWeight: 800,
        fontSize: "2rem",
      },
      h2: {
        fontFamily: "Barlow, sans-serif",
        fontWeight: 600,
      },
      body1: {
        fontFamily: "Barlow, sans-serif",
        fontWeight: 200,
      },
    },
  })
)

const IndexPage = props => (
  <>
    <ThemeProvider theme={theme}>
      <SEO title="Home" />
      <Header />
      <PageHeadBanner />
      <DescriptionSection />
      <MediaSection
        mingpao={props.data.mingpao.childImageSharp.fluid}
        singtao={props.data.singtao.childImageSharp.fluid}
      />
      <AwardsSection
        jumpstarter={props.data.jumpstarter.childImageSharp.fluid}
        ggcs={props.data.ggcs.childImageSharp.fluid}
      />
      <ContactSection />
    </ThemeProvider>
  </>
)

export const query = graphql`
  query {
    mingpao: file(relativePath: { eq: "mingpao.png" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          base64
          tracedSVG
          aspectRatio
          src
          srcSet
          srcWebp
          srcSetWebp
          sizes
          originalImg
          originalName
        }
      }
    }
    singtao: file(relativePath: { eq: "singtao.png" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          base64
          tracedSVG
          aspectRatio
          src
          srcSet
          srcWebp
          srcSetWebp
          sizes
          originalImg
          originalName
        }
      }
    }
    jumpstarter: file(relativePath: { eq: "jumpstarter.png" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          base64
          tracedSVG
          aspectRatio
          src
          srcSet
          srcWebp
          srcSetWebp
          sizes
          originalImg
          originalName
        }
      }
    }
    ggcs: file(relativePath: { eq: "ggcs.png" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          base64
          tracedSVG
          aspectRatio
          src
          srcSet
          srcWebp
          srcSetWebp
          sizes
          originalImg
          originalName
        }
      }
    }
  }
`
export default IndexPage
