import React from "react"
import { makeStyles } from "@material-ui/core/styles"
import BannerAnim from "rc-banner-anim"
import "rc-banner-anim/assets/index.css"
import * as banner1 from "../images/header.jpg"
import * as banner2 from "../images/bot1.jpeg"

const { Element } = BannerAnim
const BgElement = Element.BgElement

const useStyles = makeStyles(theme => ({
  banner: {
    height: "calc(100vw * 9/16)",
    overflow: "hidden",
  },
  bannerElement: {
    position: "relative",
  },
  bg: {
    width: "100%",
    height: "100%",
    position: "absolute",
    overflow: "hidden",
  },
}))

export const PageHeadBanner: React.FC = () => {
  const classes = useStyles()
  return (
    <BannerAnim className={`banner-anim ${classes.banner}`}>
      <Element
        key="aaa"
        prefixCls="banner-user-elem"
        className={classes.bannerElement}
      >
        <BgElement
          key="bg"
          className={classes.bg}
          style={{
            backgroundImage: `url(${banner1})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        />
      </Element>
      <Element key="bbb" prefixCls="banner-user-elem">
        <BgElement
          key="bg"
          className={classes.bg}
          style={{
            backgroundImage: `url(${banner2})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        />
      </Element>
    </BannerAnim>
  )
}
